<template>
  <div style="margin:0 auto;width:50%;">
    <h3 style="color:#20A0FF">VUE-</h3>
    <hr>
    <div style="margin:0 auto;padding-top:40px;width:600px;height:380px;text-align: left">

        <tree-transfer
          :titleLeft="titleLeft"
          :titleRight="titleRight"
          :data_left="transferData_left_area"
          :data_right="transferData_right_area"
          :showFilter="showFilter"
          :showAllCheck="showAllCheck"
          :disabled="disabled"
          @listenRightTreeInfoEvent="bindRightTreeInfoEvent"
          @listenLeftTreeInfoEvent="bindLeftTreeInfoEvent"
        ></tree-transfer>

    </div>
    <hr>
    <div style="text-align: left;font-size: 10px" >
      <span style="font-weight: bold;color:red">VUE-ELEMENT</span>
      <br>
      
      <br>
      <span style="color:blue;font-weight: bolder">[{"id":id,"name":name,"code":code,"parent":parent,"children":[]}]</span>
    </div>

  </div>
</template>

<script>
  export const areaTree = [{"id":63,"name":"","code":"cn010","parent":null,"children":[{"id":211,"name":"","code":"010","parent":63,"children":null}]},{"id":64,"name":"","code":"cn021","parent":null,"children":[{"id":213,"name":"","code":"021","parent":64,"children":null}]},{"id":67,"name":"","code":"cn0311","parent":null,"children":[{"id":221,"name":"","code":"0310","parent":67,"children":null},{"id":222,"name":"","code":"0311","parent":67,"children":null},{"id":223,"name":"","code":"0312","parent":67,"children":null},{"id":224,"name":"","code":"0313","parent":67,"children":null}]},{"id":68,"name":"","code":"cn0351","parent":null,"children":[{"id":226,"name":"","code":"0351","parent":68,"children":null},{"id":384,"name":"","code":"0349","parent":68,"children":null},{"id":385,"name":"","code":"0350","parent":68,"children":null}]},{"id":69,"name":"","code":"cn0471","parent":null,"children":[{"id":237,"name":"","code":"0471","parent":69,"children":null},{"id":238,"name":"","code":"0476","parent":69,"children":null},{"id":428,"name":"","code":"0472","parent":69,"children":null}]},{"id":70,"name":"","code":"cn024","parent":null,"children":[{"id":216,"name":"","code":"024","parent":70,"children":null},{"id":230,"name":"","code":"0411","parent":70,"children":null}]}]
  import treeTransfer  from './treeTransfer/tree-transfer'

  export default {
    components:{  treeTransfer  },
    name: 'HelloWorld',
    data () {
      return {
        disabled:false,
        titleLeft: '', //
        titleRight: '',//
        transferData_left_area:[],//
        transferData_right_area:[],//
        showFilter:true,//
        showAllCheck:true, //
      }
    },
    methods:{
      bindRightTreeInfoEvent:function(data){
        this.transferData_right_area = data;
        console.log(":"+JSON.stringify(data));
      },
      bindLeftTreeInfoEvent:function(data){
        this.transferData_left_area = data;
        console.log(":"+JSON.stringify(data));
      },
    },
    mounted() { this.transferData_left_area = JSON.parse(JSON.stringify(areaTree)) },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



